import { h, Component } from 'preact';
import Card from 'preact-material-components/Card';
import 'preact-material-components/Card/style.css';
import 'preact-material-components/Button/style.css';
import style from './style.css';
import Logo from '../../assets/osteklokken-logo.png';

export default class Home extends Component {
	render() {
		return (
			<div class="appContainer">
				<Card>
					<div class={style.cardHeader}>
						<h2 class=" mdc-typography--title">Velkommen til Osteklokkens nye app!</h2>
					</div>
					<Card.Media>
						<div>
							<img class={style.cardMedia} src={Logo} />
						</div>
					</Card.Media>
					<div class={style.cardBody}>
						Du kan se indkbslisten at bne menuen med knappen i verste venstre hjrne. Her findes mange brugbare vrktjer til ostefolket.
					</div>
				</Card>
			</div>
		);
	}
}



